<template>
  <b-modal id="errorModal"
           size="sm"
           title="Error"
           v-model="showModal"
           centered
           :header-bg-variant="headerBgVariant"
           :header-text-variant="headerTextVariant"
           :body-text-variant="bodyTextVariant">
    메일 전송에 실패했습니다.
    <div slot="modal-footer" class="w-100">
      <b-btn size="sm" class="float-right" variant="secondary" @click="showModal=false">
        Close
      </b-btn>
    </div>
  </b-modal>
</template>

<script>
  export default {
    name: "ErrorModal",
    data() {
      return {
        showModal: false,
        headerBgVariant: 'danger',
        headerTextVariant: 'light',
        bodyTextVariant: 'dark'
      }
    },
    methods: {
      openModal() {
        this.showModal = true;
      }
    }
  }
</script>

