<template>
  <div id="contact">
    <b-row>
      <b-col sm="7">
        <span>Contact me</span>
        <b-form-group class="mt-5 mb-3" id="contactForm">
          <b-row>
            <b-col sm="5" class="mb-3">
              <b-form-input id="name"
                            name="name"
                            type="text"
                            placeholder="Name"
                            class="form"
                            v-model="params.name"
                            v-validate="'required'"
                            :state="!isSendBtnClicked ? null : (!errors.has('name'))">
              </b-form-input>
              <b-form-invalid-feedback id="inputLiveFeedback">
                {{ errors.first('name') }}
              </b-form-invalid-feedback>
            </b-col>
            <b-col sm="5" class="mb-3">
              <b-form-input id="email"
                            name="email"
                            type="email"
                            placeholder="Email"
                            class="form"
                            v-model="params.email"
                            v-validate="'required|email'"
                            :state="!isSendBtnClicked ? null : (!errors.has('email'))">
              </b-form-input>
              <b-form-invalid-feedback id="inputLiveFeedback">
                {{ errors.first('email') }}
              </b-form-invalid-feedback>
            </b-col>
          </b-row>
          <b-row class="mb-3">
            <b-col sm="10">
              <b-form-input id="subject"
                            name="subject"
                            type="text"
                            placeholder="Subject"
                            class="form"
                            v-model="params.subject"
                            v-validate="'required'"
                            :state="!isSendBtnClicked ? null : (!errors.has('subject'))">
              </b-form-input>
              <b-form-invalid-feedback id="inputLiveFeedback">
                {{ errors.first('subject') }}
              </b-form-invalid-feedback>
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="10">
              <b-form-textarea id="message"
                               name="message"
                               class="form"
                               type="text"
                               v-model="params.message"
                               placeholder="Message"
                               :rows="7"
                               :max-rows="10"
                               v-validate="'required|min:10'"
                               :state="!isSendBtnClicked ? null : (!errors.has('message'))">
              </b-form-textarea>
              <b-form-invalid-feedback id="inputLiveFeedback">
                {{ errors.first('message') }}
              </b-form-invalid-feedback>
            </b-col>
          </b-row>
        </b-form-group>
        <b-row>
          <b-col sm="10">
            <b-button size="sm" id="sendBtn" @click="send">SEND</b-button>
          </b-col>
        </b-row>
      </b-col>
      <b-col sm="5">
        <div id="map"></div>
      </b-col>
    </b-row>

    <vue-snotify></vue-snotify>
    <ErrorModal ref="errorModal"></ErrorModal>

  </div>
</template>

<script>
  import Vue from 'vue';
  import VeeValidate from 'vee-validate';
  import Snotify, {SnotifyPosition} from 'vue-snotify'
  import ErrorModal from "./ErrorModal";

  const options = {
    toast: {
      position: SnotifyPosition.rightTop
    }
  }

  Vue.use(VeeValidate);
  Vue.use(Snotify, options);


  export default {
    name: "contact",
    components: {
      ErrorModal
    },
    data() {
      return {
        params: {
          name: '',
          email: '',
          subject: '',
          message: ''
        },
        isSendBtnClicked: false
      }
    },
    created() {
      this.passTheStatus('contact');
    },
    mounted() {
      this.createMap();
    },
    methods: {
      createMap() {
        let container = document.getElementById('map'); //지도를 담을 영역의 DOM 레퍼런스
        let options = { //지도를 생성할 때 필요한 기본 옵션
          center: new daum.maps.LatLng(35.189992, 129.120152), //지도의 중심좌표.
          level: 9 //지도의 레벨(확대, 축소 정도)
        };

        let map = new daum.maps.Map(container, options); //지도 생성 및 객체 리턴

        // 마커가 표시될 위치입니다
        var markerPosition = new daum.maps.LatLng(35.189992, 129.120152);

        // 마커를 생성합니다
        var marker = new daum.maps.Marker({
          position: markerPosition
        });

        // 마커가 지도 위에 표시되도록 설정합니다
        marker.setMap(map);
      },
      send() {
        this.isSendBtnClicked = true;
        this.$validator.validateAll()
          .then(result => {
            if (result) {
              const emailjs = require('emailjs-com');
              const that = this;
              emailjs
                .send(
                  process.env.VUE_APP_EMAIL_SERVICE_ID,
                  process.env.VUE_APP_EMAIL_TEMPLATE_ID,
                  that.params,
                  process.env.VUE_APP_EMAIL_USER_ID
                ).then(function (response) {
                that.$snotify.success('메일을 전송했습니다.', 'Success', {
                  timeout: 3000,
                  showProgressBar: true,
                  closeOnClick: true,
                  pauseOnHover: true
                });
                that.reset();
                that.isSendBtnClicked = false;
              }, function (error) {
                console.log('FAILED...', error);
                that.$refs.errorModal.openModal();
              });
            }
          });
      },
      reset() {
        this.params.name = '';
        this.params.email = '';
        this.params.subject = '';
        this.params.message = '';
      }
    }
  }
</script>

<style scoped>
  #sendBtn {
    background-color: transparent;
    border: 1px solid #e67e22;
    color: #e67e22;
    float: right;
  }

  #sendBtn:hover {
    background-color: #e67e22;
    color: #3d3d3d;
  }

  .form {
    background-color: #3d3d3d;
    border: 1px solid #e67e22;
    color: #bdc3c7;
  }
</style>